body{
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
h1{
  font-size: 34px;
  position: relative;
  margin-top: 70px;
  padding-bottom: 6px;
}
h1::after{
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  height: 3px;
  width: 100%;
  background: #5454c1;
}

