table.calculator {
  background-color: #FFFFFF;
  margin: 0 auto;
  margin-top: 20px;
  margin-right: 10px;
  margin-left: 10px;
  margin-bottom: 40px;
  border-radius: 10px;
  background: #ececec;
  padding: 20px 10px 23px 10px;

}

.calculator td {
  width: 110px;
  height: 60px;
  text-align: center;
  font-size: xx-large;

}

.calculator #screen {
  text-align: right;
  padding: 0 13%;
}

.calculator .orange {
  color: #ff3300;
}

.calculator td:active {
  background-color: #e0e0d1;
  border-radius: 20px;
}
.SnakeGame td {
  width: 45px;
  height: 45px;
  background: #202124;
}

.SnakeGame td.active {
  background: green;
}

.SnakeGame td.activeRed {
  background: red;
}

.SnakeGame td.active2 {
  background: blue;
}

.SnakeGame-control tr {
  color: #202124;
  width: 22px;
  height: 22px;
}

.SnakeGame-control td {
  width: 30px;
  height: 30px;
}

.SnakeGame-control #up,
.SnakeGame-control #right,
.SnakeGame-control #down,
.SnakeGame-control #left {
  cursor: pointer;
}

.SnakeGame-control #up:hover,
.SnakeGame-control #right:hover,
.SnakeGame-control #down:hover,
.SnakeGame-control #left:hover {
  font-size: 1.2em;
}